.App {
    font-family: sans-serif;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background: linear-gradient(to bottom, rgba(173, 216, 230, 0.8), rgba(240, 248, 255, 0.9)); 
  }
  
  .Todo {
    text-align: center;
    width: 50%;
    padding: 20px;
    margin: auto;
    justify-content: center;
    border-radius: 20px;
    background: rgba(255, 255, 255, 0.8); 
    backdrop-filter: blur(10px);
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    padding: 20px;
    overflow: hidden; 
  }
  
  ul {
    list-style-type: none;
    padding: 1%;
    margin: 0;
    justify-content: center;
    text-align: center;
    
  }
  
  button {
    width: 70px;
    height: 35px;
    background-color: rgb(124, 197, 249);
    border: none;
    font-size: 15px;
    font-weight: 800;
    border-radius: 4px;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    cursor: pointer;
  }
  
  .input {
    border: none;
    width: 340px;
    height: 35px;
    border-radius: 9px;
    text-align: center;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  }
  
  .Top {
    display: flex;
    justify-content: center;
    gap: 12px;
  }
  
  li {
    display: flex;
    padding: 10px;
   
  }
  
  

  .task {
    word-wrap: break-word;
    background-color: aliceblue;
    height: auto;
    border: none;
    border-radius: 9px;
    text-align: center;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    margin-top: 1%;
    overflow-wrap: break-word;
    align-items: center;
    display: flex;
    margin: 5px 0;
    justify-content: space-between;
  }

  .done {
    background-color: #91f8a9;
    color: #155724; 
}

.done button {
    background-color: #9bf7b0; 
    color: white;
}

.del{
  background-color: rgb(241, 117, 117);
}
.del.done{
  background-color: rgb(241, 117, 117);
}
@media (max-width: 600px) {
  .Top {
      flex-direction: column; 
      align-items: stretch;
  }

  .input {
      margin-right: 0; 
      margin-bottom: 10px; 
      width: 100%;
  }

  
  .btn {
      width: 100%; 
  }

  .task {
      flex-direction: column; 
      align-items: flex-start; 
  }

  .task div {
      width: 100%; 
      display: flex;
      justify-content: space-between; 
}
.done , .del{
  margin-top: 7%;
}
}